<template>
  <div class="upload-container">
    <el-upload class="upload-demo"
      action=""
      :limit="2"
      accept=".zip"
      :before-upload="beforeUpload"
      :file-list="FileList"
      :on-success="(response, file, fileList) => {
          handleChange(response, file, fileList,scope)
      }"
      
      >
      <el-button size="small" type="primary" v-loading="loading">点击上传</el-button>
      <el-button size="small" type="primary">解析矢量</el-button>
      <div slot="tip" class="el-upload__tip">只能上传shapefile格式的.zip文件</div>
    </el-upload>
  </div>
</template>
<script>
import shp from 'shpjs/dist/shp';
// :auto-upload="false"
export default {
  name: "loadLocalShp",
  props: {},
  data() {
      return {
        loading:false,
        annexHttpRequest:[],
        FileList: []
      }
  },
  beforeMount(){
    
  },
  mounted() {

  },
  beforeDestroy(){

  },
  destroyed(){ 
        
  },
  methods: { 
    // 上传前验证是否是压缩文件
    beforeUpload(file){
      let fileName = file.name.split(".")[1];
      if (fileName != "zip") {
        this.$message.error("只能上传zip格式的文件!");
        return;
      }
    }, 
    handleRemove(){

    },
    // 点击上传
    handleChange(file, fileList) {
      // console.log('file:', file, this.fileList);
      this.FileList = fileList;
      // let fileName = file.name.split(".")[1];
      // if (fileName === "zip") {
      //   console.log(file.name);
      //   // this.loading = true;
      //   this.generateFeature(file.raw);
      //   console.log('file.zip:', file);
      // } 
    },
    // 提交到服务器并生成矢量
    generateFeature(file){
      // 
      console.log('file:', file);
      var reader = new FileReader();
      //将文件以二进制形式读入页面
      reader.readAsArrayBuffer(file);
      reader.onload =  (result)=> {
      var fileData = result; //fileData就是读取到的文件的二进制数据
      shp(fileData).then(function (geojson) {
        console.log('geojson:', geojson);
      });
      }
      // this.addShapefileToMap();
    },
    // 添加矢量到地图
    addShapefileToMap(feature) {
      this.loading = false;
    }
    
  }
}
</script>

<style scoped>
.upload-container{
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
}
</style>